import {mapMutations, mapGetters} from 'vuex';
import drag from './drag';
export default {
  data() {
    return {
      isdrag: false
    };
  },
  components: {drag},
  methods: {
    ...mapMutations([
      'increase'
    ]),
    decrease(){
      this.$store.commit('decrease');
      this.$vux.alert.show({
        title: 'Vux is Cool',
        content: 'Do you agree?'
      })
    },
    ondrag() {
      console.log('123');
      this.isdrag = !this.isdrag
    }
  },
  computed: {
    ...mapGetters([
      'count'
    ])
  }
};



