export default {
  data() {
    return {
      demoA: true,
      touchWord: '测试：左/右滑'
    };
  },
  components: {},
  methods: {
    onAfterLeave(){
      alert('animate leave end');
    },
    onSwipeLeft(){
      this.touchWord = 'left'
    },
    onSwipeRight(){
      this.touchWord = 'right'
    }
  }
};



