
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import {getGiftCategory,getGifts} from '@/service/getData'
export default {
  data() {
    return {
      isShowReceive: true,
      isShowPresent1: false,
      isShowPresent2: true,
      giftCategory: [],
      categoryId: 1
    };
  },
  components: {Swiper, SwiperItem, Scroller},
  async beforeMount() {
    let respponse = await getGiftCategory()
    this.giftCategory = respponse.data.list
    let res = await getGifts(this.categoryId)
    console.log(res)
  },
  methods: {
    onToggleReceive(){
      this.isShowReceive = !this.isShowReceive;
    },
    onTogglePresent(){
      if (!this.isShowPresent1) {
        this.isShowPresent1 = !this.isShowPresent1;
      } else {
        this.isShowPresent2 = !this.isShowPresent2;
      }
    }
  }
};



