import '../plugin/transform'
export default {
  data() {
    return {};
  },
  props: {
    draggable: {type: Boolean}
  },
  mounted() {
    $(this.$refs.box).transform()
  },
  components: {},
  watch: {
    draggable: function (newvalue) {
        if(newvalue){
          $(this.$refs.box).hide();
        }else{
          $(this.$refs.box).show();
        }
    }
  }
};



